.home-main{
    color: var(--primary-black);
    overflow-y: auto;
    overflow-x: hidden;
    position: relative;
    box-shadow: -5px 0 10px -5px rgba(0, 0, 0, 0.5);
}

.home-main .vendor-index-container:first-child{
    margin-top: 30px
}

.home-main>*:not(:last-child):not(.address-form-container)::after{
    content: '';
    width: 100%;
    border-bottom: 2px solid var(--secondary-white);
    display: block;
    margin-top: 30px;
}

@media screen and (max-width: 599px){
    .home-main{
        margin-left: 0;
        overflow-x: auto;
    }
}