.home-nav{
    color: var(--primary-red);
    background-color: var(--primary-white);
    display: flex;
    justify-content: space-between;
    height: 70px;
    width: 100%;
    user-select: none;
    position: sticky;
    top: 0;
    left: 0;
    z-index: 20;
    box-shadow: -5px 0 10px -5px rgba(0, 0, 0, 0.5);
}

.location-btn{
    background-color: var(--primary-yellow);
    font-size: 12px;
    max-height: 30px;
    padding-left: 11px;
    padding-right: 11px;
    margin-left: 20px;
    width: auto;
}

.user-address{
    font-size: 15px;
    padding: 0 40px;
    color: var(--primary-gray);
}

.location-btn:hover{
    background-color: var(--primary-yellow);
    cursor: auto;
}

.main-logo{
    width: 130px;
    margin: 0 10px;
}


