/* =========================== */

.hamburger-menu{
    fill: var(--primary-gray);
    color: var(--primary-gray);
    flex-shrink: 0;
}

.home-nav svg:hover{
    cursor: pointer;
}

.logo-container{
    display: flex;
    align-items: center;
    cursor: pointer;
}

.home-nav-left{
    /* width: 40%; */
    display: flex;
    align-items: center;
    /* justify-content: space-between; */
    margin-left: 5%;
}

.home-nav-left-inner-container{
    width: 25vw;
    margin-left: 5%;
    display: flex;
    align-items: center;
    /* justify-content: space-between; */
}

/* .home-nav-left img{
    max-width: 35px;
    height: auto;
} */

.home-nav-right{
    /* margin-right: 7%; */
    display: flex;
    align-items: center;
}

.home-nav-right svg{
    color: var(--primary-gray);
    flex-shrink: 0
}

@media screen and (max-width: 599px) {
    .home-nav h1{
      display: none;
    }

    .user-address{
        padding: 10px;
        width: 150px;
    }

    .location-btn{
        display: none;
    }
}
